import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhAlgorithm, VhQuery } from 'ionic-vhframeworks';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import jsbarcode from 'jsbarcode';
import printJS from 'print-js';
@Component({
  selector: 'app-print-five-col',
  templateUrl: './print-five-col.component.html',
  styleUrls: ['./print-five-col.component.scss']
})
export class PrintFiveColComponent implements OnInit {
  propertySelected: any;
  stamp: any;
  listRowBarcode = [{show_col2:false,show_col3:false,show_col4:false,show_col5:false}]; // chứa số hàng hiển thị. true thì hiển thị 2 tem. false là hien thị 1 tem trên 1 hàng
  heightScroll :any;
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private cdf: ChangeDetectorRef,
    private vhComponent:VhComponent,
    private vhQuery:VhQuery
  ) {
    this.stamp = this.router.getCurrentNavigation().extras.state;
    this.stamp.barcode.property =  this.vhAlgorithm.sortNumberbyASC(this.stamp.barcode.property,'y')
  }

  ngOnInit(): void {
    for(let item of this.stamp.barcode.property){
      switch(item.type){
        case 'barcode-text': item.value = this.stamp.formProduct.barcode;break;
        case 'price': item.value = this.stamp.formProduct.price;break;
        case 'barcode': item.value = this.stamp.formProduct.barcode;break;
        case 'name': item.value = this.stamp.formProduct.name;break;
      }
    }
    this.changeNummber()
  }

  changeNummber() {
    /**
     * nếu chia hết cho 5 thì hiện cả 5 tem trên hàng
     * ngược lại => khi push tới hàng cuối cùng thì check i = (number-1)/5 : cột thứ 1  hay = (number-2)/5 : cột thứ 2 hay = (number-3)/5 : cột thứ 3 hay = (number-4)/5 : cột thứ 4
     * 
     */
    this.listRowBarcode = []
    for (let i = 0; i < this.stamp.number/5; i++) {
     if(this.stamp.number%5 ==0)  this.listRowBarcode.push({show_col2:true,show_col3:true,show_col4:true,show_col5:true});
     else {
       if(i == (this.stamp.number-1)/5) this.listRowBarcode.push({show_col2:false,show_col3:false,show_col4:false,show_col5:false});
       else if(i == (this.stamp.number-2)/5) this.listRowBarcode.push({show_col2:true,show_col3:false,show_col4:false,show_col5:false});
       else  if(i == (this.stamp.number-3)/5) this.listRowBarcode.push({show_col2:true,show_col3:true,show_col4:false,show_col5:false});
       else  if(i == (this.stamp.number-4)/5) this.listRowBarcode.push({show_col2:true,show_col3:true,show_col4:true,show_col5:false});
       else this.listRowBarcode.push({show_col2:true,show_col3:true,show_col4:true,show_col5:true});
     }
    }
    
  }

  ngAfterViewChecked(): void {
    if (document.querySelector(".barcode"))
      this.renderBarcode();
    if(document.querySelector('#scroll')) this.heightScroll = document.querySelector('#scroll').clientHeight -32;
    this.cdf.detectChanges();
  }

  renderBarcode() {
    if(this.stamp.formProduct.barcode)
    jsbarcode(".barcode",  this.stamp.formProduct.barcode, {
      displayValue: false,
      // height: 65
    });
  }

  goBack() {
    this.router.navigate(['/cafe/dashboard/management/design/design-label/label-dish/detail'],{state: this.stamp.barcode})
  }


  printBrowser() {
    printJS({
      printable: "stamp-barcode",
      type: "html",
      scanStyles: false,
      maxWidth: this.stamp.width,
      style:
        '@page { margin: 0 !important;padding: 0 !important;}' +
        '.stamp {margin-bottom: 3mm; width: ' + this.stamp.width + 'mm;height: ' + (this.stamp.height - 4) + 'mm;}' +
        '.center-all-content { display: flex;align-items: center;justify-content: center;}'
    });
  }
}
