import { NzModalService } from 'ng-zorro-antd/modal';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuery, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
@Component({
  selector: 'app-label-dish',
  templateUrl: './label-dish.component.html',
  styleUrls: ['./label-dish.component.scss']
})
export class LabelDishComponent implements OnInit {
  list_barcode: any = [];
  showModalAdd = false;
  showModalEdit = false;
  id_barcode: any = '';
  formAdd: FormGroup;
  barcode_name = ''
  constructor(
    private router: Router,
    public languageService: LanguageService,
    private nzModalService: NzModalService,
    private vhQuery: VhQuery,
    private vhComponent: VhComponent,
    private vhQueryCafe: VhQueryCafe,
    private vhAlgorithm: VhAlgorithm,
    private vhAuth: VhAuth
  ) { }

  ngOnInit(): void {
    this.getData();
    this.initForm();
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/design/design-label']);
  }

  getData() {
    // this.vhComponent.showLoading('').then(() => {
    this.vhQuery.getDocsByFields("barcode_designs", { id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id }, type : { $eq : 1} })
      .then((response: any) => {
        this.list_barcode = response.filter(item => item.type != 2 || item.type != 3 || item.type != 4)
        // this.vhComponent.hideLoading(0);
        console.log(response);

      })
    // })
  }

  initForm() {
    this.formAdd = new FormGroup({
      name: new FormControl('', Validators.required),
      col: new FormControl(1, Validators.required),
      width: new FormControl(40, Validators.required),
      height: new FormControl(30, Validators.required),
      template: new FormControl(1, Validators.required),
      default: new FormControl(false, Validators.required),
      status: new FormControl(true, Validators.required),
      type: new FormControl(1),
    })
  }
  addBarcode() {
    console.log(this.formAdd.value);

    if (!this.list_barcode.length) this.formAdd.controls['default'].setValue(true)
    let w = this.formAdd.value.width
    let h = this.formAdd.value.height
    if (h > w) { this.formAdd.controls['width'].setValue(h); this.formAdd.controls['height'].setValue(w); }
    let prop =
    {
      header: {
        type: 'header',
        font_size: 12,
        text_align: 'left',
        status: true,
        value: this.formAdd.value.template == 1 ? 'Tên nhà hàng' : 'Bàn 01 - Khu A',
        font_weight: 'bold',
        font_style: 'normal', // normal | italic
        text_decoration: 'none', // underline | none
        width: w * 0.7 - 1,
        height: h * 0.13,
        x: 0, y: 0
      },
      name: {
        type: 'name',
        font_size: 12,
        text_align: 'left',
        status: true,
        value: '<span>Tên món ăn</span> <br> <span>+ Thuộc tính 1</span> <br> <span>+ Món phụ 1</span>',
        font_weight: '500', // bold | 500
        font_style: 'normal', // normal | italic
        text_decoration: 'none', // underline | none
        width: w,
        height: h * 0.55,
        x: 0, y: 17
      },
      total: {
        type: 'total',
        font_size: 12,
        text_align: 'left',
        status: true,
        value: 30000,
        font_weight: 'bold', // bold | 500
        font_style: 'normal', // normal | italic
        text_decoration: 'none', // underline | none
        width: w * 0.5,
        height: h * 0.13,
        x: 0, y: 113
      },
      note: {
        font_size: 12,
        text_align: 'left',
        status: true,
        value: 'Ghi chú của món ăn',
        font_weight: '500', // bold | 500
        font_style: 'normal', // normal | italic
        text_decoration: 'none', // underline | none
        type: 'note',
        width: w,
        height: h * 0.13,
        x: 0, y: 96
      },
      time: {
        font_size: 10,
        text_align: 'right',
        status: true,
        value: new Date(),
        font_weight: '500', // bold | 500
        font_style: 'normal', // normal | italic
        text_decoration: 'none', // underline | none
        width: w * 0.3,
        height: h * 0.13,
        type: 'time',
        x: w * 0.75 * 3.78, y: 0
      },
      number: {
        type: 'number',
        font_size: 10,
        text_align: 'right',
        status: true,
        value: '3/4',
        font_weight: '500', // bold | 500
        font_style: 'normal', // normal | italic
        text_decoration: 'none', // underline | none
        width: w * 0.5,
        height: h * 0.13,
        x: w * 0.5 * 3.78, y: 113
      },
      notice: {
        font_size: 10,
        text_align: 'left',
        status: true,
        value: 'Sologan của nhà hàng',
        font_weight: '500', // bold | 500
        font_style: 'italic', // normal | italic
        text_decoration: 'none', // underline | none
        width: w,
        height: h * 0.11,
        type: 'notice',
        x: 0, y: 130
      },
    }

    this.vhComponent.showLoading('').then(() => {
      this.vhQuery.addDoc("barcode_designs", {
        ...this.formAdd.value, id_branch: this.vhQueryCafe.getDefaultBranch()._id, property: prop
      })
        .then((response) => {
          this.vhComponent.hideLoading(0);
          this.list_barcode.push(response);
          this.showModalAdd = false;
        }, error => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("An error while adding price list"))
        })
    })

  }

  editBarcode() {
    if (this.barcode_name != '')
      this.vhComponent.showLoading('').then(() => {
        this.vhQuery.updateDoc("barcode_designs", this.id_barcode, { name: this.barcode_name }).then((bool) => {
          this.vhComponent.hideLoading(0);
          this.showModalEdit = false;
          this.list_barcode[this.list_barcode.findIndex(item => item._id == this.id_barcode)].name = this.barcode_name;
        }, error => {
          this.vhComponent.hideLoading(0);
          console.log('error', error);
        })
      })

  }

  updateStatus(item,value) {
    this.vhComponent.showLoading('').then(() => {
      this.vhQuery.updateDoc("barcode_designs", item._id, { status: value}).then((bool) => {
        this.vhComponent.hideLoading(0); 
        item.status = value
      }, error => {
        this.vhComponent.hideLoading(0);
        console.log('error', error);
      })
    })

  }
  setDefault(item, i) {
    this.vhComponent.showLoading('').then(() => {
      let index = this.list_barcode.findIndex(i => i.default && i._id != item._id)
      if (index != -1) {
        Promise.all([this.vhQuery.updateDoc("barcode_designs", item._id, { default: true }), this.vhQuery.updateDoc("barcode_designs", this.list_barcode[index]._id, { default: false })])
          .then(() => {
            this.getData()
            this.vhComponent.hideLoading(0)
          })
      } else {
        this.vhQuery.updateDoc("barcode_designs", item._id, { default: true })
          .then(() => {
            this.getData()
            this.vhComponent.hideLoading(0)
          })
      }
    })
  }

  gotoEdit(item) {


    this.router.navigate(['/cafe/dashboard/management/design/design-label/label-dish/template' + item.template.toString()], { state: item });
    // switch(item.col){
    //   case 2:{
    //     this.router.navigate(['/cafe/dashboard/management/design-barcode/detail'], { state: item });
    //     break;
    //   }
    //   case 3:{
    //     this.router.navigate(['/cafe/dashboard/management/design-barcode/detail-three-col'], { state: item });
    //     break;
    //   }
    //   default :{
    //     this.vhComponent.alertMessageDesktop( "error",this.languageService.translate("Không hỗ trợ kiểu tem này"))
    //   }
    // }
  }

  deleteBarcode(item, index) {
    if (item.default) this.nzModalService.confirm({
      nzTitle: this.languageService.translate("Mẫu tem này đang được thiết lập mặc định! Xác nhận xóa?"),
      nzCancelText: this.languageService.translate("Cancel"),
      nzOnOk: () => {
        this.vhComponent.showLoading('').then(() => {

          this.vhQuery.deleteDoc("barcode_designs", item._id)
            .then((bool) => {
              this.list_barcode.splice(index, 1)
              this.vhComponent.hideLoading(0);
            })
        })
      }
    })
    else
      this.nzModalService.confirm({
        nzTitle: this.languageService.translate("Are you sure to delete this label template?"),
        nzCancelText: this.languageService.translate("Cancel"),
        nzOnOk: () => {
          this.vhComponent.showLoading('').then(() => {

            this.vhQuery.deleteDoc("barcode_designs", item._id)
              .then((bool) => {
                this.list_barcode.splice(index, 1)
                this.vhComponent.hideLoading(0);
              })
          })
        },
      })
  }

}
