import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LabelDishComponent } from './label-dish.component'; 
import { RouterModule, Routes } from '@angular/router'; 
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import {DragDropModule} from '@angular/cdk/drag-drop'; 
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module'; 
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DetailComponent } from './detail/detail.component';
import { PrintTwoColComponent } from './print-two-col/print-two-col.component';
import { PrintThreeColComponent } from './print-three-col/print-three-col.component';
import { PrintOneColComponent } from './print-one-col/print-one-col.component';
import { PrintFourColComponent } from './print-four-col/print-four-col.component';
import { PrintFiveColComponent } from './print-five-col/print-five-col.component';
import { Template1Component } from './template1/template1.component';
import { Template2Component } from './template2/template2.component';
const routes: Routes = [
  {
    path: '',
    component: LabelDishComponent
  },
  {
    path: 'template1',
    component:Template1Component
  },
  {
    path: 'template2',
    component:Template2Component
  },
  {
    path: 'print-two-col',
    component:PrintTwoColComponent
  },
  {
    path: 'print-three-col',
    component:PrintThreeColComponent
  },
  {
    path: 'print-one-col',
    component:PrintOneColComponent
  },
  {
    path: 'print-four-col',
    component:PrintFourColComponent
  },
  {
    path: 'print-five-col',
    component:PrintFiveColComponent
  },
]



@NgModule({
  declarations: [
    LabelDishComponent,
    DetailComponent,
    PrintTwoColComponent,
    PrintThreeColComponent,
    PrintOneColComponent,
    PrintFourColComponent,
    PrintFiveColComponent,
    Template1Component,
    Template2Component,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    DragDropModule
  ]
})
export class LabelDishModule { }
