import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhAlgorithm, VhQuery } from 'ionic-vhframeworks';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import jsbarcode from 'jsbarcode';
import printJS from 'print-js';

@Component({
  selector: 'app-print-two-col',
  templateUrl: './print-two-col.component.html',
  styleUrls: ['./print-two-col.component.scss']
})
export class PrintTwoColComponent implements OnInit {
  
  ratio : number = 3;
  stamp: any;
  root : any; 
  number = 1;
  listRowBarcode = [false]; // chứa số hàng hiển thị. true thì hiển thị 2 tem. false là hien thị 1 tem trên 1 hàng
  heightScroll :any; 
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private cdf: ChangeDetectorRef,
    private vhComponent:VhComponent,
    private vhQuery:VhQuery
  ) {
    this.stamp = this.router.getCurrentNavigation().extras.state.barcode; 
    this.root = this.router.getCurrentNavigation().extras.state
     switch(this.stamp.width ){
       case 40: {this.ratio = 6;break}
       case 50: {this.ratio = 3;break}
     }
  }

  ngOnInit(): void {
     
    this.changeNummber()
  }

  changeNummber() {
    this.listRowBarcode = []
    for (let i = 0; i < this.root.number/2; i++) {
     if(this.root.number%2 ==0)  this.listRowBarcode.push(true);
     else {
       if(i == (this.root.number-1)/2) this.listRowBarcode.push(false);
       else this.listRowBarcode.push(true);
     }
    }
  }

  ngAfterViewChecked(): void { 
    if(document.querySelector('#scroll')) this.heightScroll = document.querySelector('#scroll').clientHeight -32;
    this.cdf.detectChanges();
  } 
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/design/design-label/template'+this.stamp.this.stamp ],{state: this.stamp})
  } 

  printBrowser() {
    printJS({
      printable: "stamp-barcode",
      type: "html",
      scanStyles: false,
      maxWidth: this.stamp.width,
      style:
        '@page { margin: 0 !important;padding: 0 !important;}' +
        '.stamp {margin-bottom: 3mm; width: ' + this.stamp.width + 'mm;height: ' + (this.stamp.height - 4) + 'mm;}' +
        '.center-all-content { display: flex;align-items: center;justify-content: center;}'
    });
  }

}
